<template>
  <div class="dashboard">

    <template>
      <form>
        <v-text-field
            type="text"
            v-model="loginBody.userName"
            label="Username"
            required

        ></v-text-field>
        <v-text-field
            type="password"
            v-model="loginBody.userPassword"
            label="Password"
            required
        ></v-text-field>
        <v-btn
            rounded
            elevation="1"
            class="mr-4"
            @click="logIn"
        >
          Log in
        </v-btn>

        <v-btn
            color="primary"
            rounded
            elevation="1"
            @click="logOut"
        >
          Log Out
        </v-btn>

      </form>
    </template>

  </div>
</template>

<script>

let loginBody = {
  "userName": "",
  "userPassword": ""
}

export default {
  data() {
    return {
      loginBody
    }
  },
  methods: {
    logIn() {
      console.log(this.loginBody)
      this.$http.post(this.$host + `/intraweb/login/`, this.loginBody)
          .then(result => {
            console.log(result.data)
            //Store token
            let token = result.data.jwt;
            localStorage.setItem('user-token', token);
            this.$http.defaults.headers.common["Authorization"] = "Bearer " + token;
          })
    },
    logOut(){
      console.log("You clicked LOG OUT.")
      location.reload()
      localStorage.removeItem('user-token');
    }
  }

}
</script>
